/* eslint-disable no-script-url */
import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import {format} from "date-fns";
import { makeStyles } from '@material-ui/core/styles';

import {
    CssBaseline, Typography, Container, Table, Link,
    Grid
} from '@material-ui/core';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import { Context } from '../context';


const useStyles = makeStyles(theme => ({
    seeMore: {
        marginTop: theme.spacing(3),
    },
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
}));
function formatDate(d) {
    const date = new Date(d);
    return format(date, 'yyyy/MM/dd HH:mm')
}

export default function Records() {
    const classes = useStyles();

    const records = React.useContext(Context).records;

    const handleExport = e => {
        const element = document.createElement("a");
        const file = new Blob([JSON.stringify(records.list)], { type: 'text/plain' });
        element.href = URL.createObjectURL(file);
        element.download = "myFile.txt";
        document.body.appendChild(element); // Required for this to work in FireFox
        element.click();
    }

    const handleDelete = row => e => {
        records.delete(row)
    }
    return (
        <React.Fragment>
            <Container component="main" maxWidth="xs">

                <CssBaseline />

                <div className={classes.paper}>
                    {/* <Grid container spacing={2}>
                        <Grid item xs={6} sm={6}>
                            
                        </Grid>
                        <Grid item xs={6} sm={6}>
                            
                        </Grid>
                    </Grid> */}

                    <Link variant="body1" component={RouterLink} to="/">
                        Back
                            </Link>
                    <Link variant="body1" onClick={handleExport}>
                        Export
                            </Link>

                    <Table size="small">
                        <TableHead>
                            <TableRow>
                                <TableCell>Date</TableCell>
                                <TableCell>Location</TableCell>
                                {/* <TableCell align="right">Sale Amount</TableCell> */}
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {
                                records.list.map((row, index) => (
                                    <TableRow key={index}>
                                        <TableCell>{formatDate(row.dateTime)}</TableCell>
                                        <TableCell>{row.location}</TableCell>

                                        <TableCell align="right">
                                        <Link variant="body1" onClick={handleDelete(row)}>
                                        Delete
                                            </Link>
                                        </TableCell>
                                    </TableRow>
                                ))
                            }
                        </TableBody>
                    </Table>
                </div>


            </Container>
        </React.Fragment>

    );
}